import React, {useState, useContext} from 'react';
import { withStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import PropTypes from 'prop-types';
import Button from '@material-ui/core/Button';
import Image from './Image/page1';
import Feedback from '../FeedbackBox/FeedBackBox';

import ScoreContext from '../../Context/score-context';

import classed from './TextDual.css';

const styles = theme => ({
    root: {
      '& > *': {
        margin: theme.spacing(1),
        width: '8ch',
      },
    },
});

const TextDual = (props) => {
  const { classes } = props;
  const setCurScore = useContext(ScoreContext);
  const [answer, SetAnswer] = useState([0.1, 50]);
  const [powerOf, setPowerOf] = useState('11');
  const [title, setTitle] = useState('');
  const [value, setValue] = useState('');
  const [power, setPower] = useState('');
  const [error, setError] = useState(null);
  const [helperText, setHelperText] = useState('');
  const [questAnswered, setQuestAnswered] = useState(false);
  const handleSubmit = event => {
    event.preventDefault();

    if ((value >= answer[0] && value <= answer[1]) && power === powerOf) {
      setTitle('Well Done!!!!')
      setHelperText('Congratulations you have calculated the correct number of receptors in the tissue.');
      calcScore(1)
    } else {
      setTitle('Incorrect')
      setHelperText('Incorrect you have not calculated the correct number of receptors in the tissue.');
      calcScore(0);
    }
    setQuestAnswered(true);
    setError(true);
    props.nextBut(true);
   // setValue('');
   // setPower('');
  };

  const calcScore = (score) =>{
    let updatedScore = [];
    //console.log('setCurScore.status: ',setCurScore.status.scores)
    updatedScore = setCurScore.status.scores;
    updatedScore[7] = score;
    //console.log('Table updatedScore: ',updatedScore)
    setCurScore.setScre(updatedScore)
  };

  const inputData = (event) =>{

    //console.log(event.target.id)
    if(event.target.id === 'userAns'){
      setValue(event.target.value);
    }else{
      setPower(event.target.value);
    }
    
    setHelperText('');
    setError(null);
  }

  return (
    <div className={classed.Ruled}>
        <div className={classed.box}>
        <div>
            {error ? <Feedback title={title} feedback={helperText}/> : null}
        </div>
        <div className={classed.Left}>
            
                <div className={classed.questText}>Calculate the number of receptors in the tissue.  (Avogadros number = 6.02 X 10<sup>23</sup> ).</div>
                <div className={classed.promptText}>Please enter your answer in the space provided, then click <b>Submit</b>.</div>
                <div className={classed.InputForm}>
                  <div>
                    <b>Answer:</b>
                  </div>
                  <div>
                    <form className={classes.root} autoComplete="off">
                      <TextField id="userAns" variant="outlined" value={value} onChange={inputData.bind(this)} disabled={questAnswered}/>
                    </form>
                  </div>
                  <div>
                    <b>X 10 </b>
                  </div>
                  <div className={classed.supText}>
                     <form className={classes.root} autoComplete="off">
                     <TextField id="powerAns" variant="outlined" value={power} onChange={inputData.bind(this)} disabled={questAnswered}/>
                    </form>
                  </div>
                  <div>
                    <b>receptors/mg of tissue  </b>
                  </div>
                </div>
                <div className={classed.questText}><b>Hint:</b> <i>Bmax is the maximum concentration of drug bound to receptor</i></div>   
                <div className={classed.Button}>
                  <Button type="submit" variant="contained" color="secondary" className={classes.button} disabled={questAnswered} onClick={handleSubmit.bind(this)}>
                    SUBMIT
                  </Button>
                </div>
          </div>
          <div className={classed.Right}>
              <Image />   
          </div>
      </div>
    </div>
  );
}

TextDual.propTypes = {
    classes: PropTypes.object.isRequired,
  };
  
export default withStyles(styles)(TextDual);